        pld,
},
    name:'proto',
    data()
    {
        return{

            colors: ['#99A9BF', '#F7BA2A', '#FF9900'],
            value_user:null,

            IIDD:301,
            stuff:{
                value2:Math.floor(Math.random()*(5))+1,
                picadd:'http://r.photo.store.qq.com/psc?/V12aZVkM2Rfh0C/YWvjNfAyIVey1fwA2tD8GE2tCcGMwvotlTagHK3Q2H1HLvtnQ9EMUCgP9efXZ49xp.v5cyLmdg0ApTwJFJlR5B1.OPT.RG**8FvgHnqzRec!/r',
