                imdb:'https://www.imdb.com/title/tt9850064/',
                roughtime:'(2019)',
                title:'海兽之子',
                director:'渡边步',
                scriptor:'木之花咲 / 五十岚大介',
                mainact:'芦田爱菜 / 石桥阳彩 / 浦上晟周 / 森崎温 / 稻垣吾郎',
                type:'动画 / 奇幻 / 冒险',
                place:'日本',
                dtime:'2020(中国大陆) / 2019-06-07(日本)',
                lang:'日语',
                long:'111分钟',
                webtxt:'    偶遇到神秘少年“海”（石桥阳彩配）和“空”（浦上晟周配）的这个炎炎夏天，是琉花（芦田爱菜配）暑期的正式开始。天真无邪的“海”，看透一切的“空”，海洋馆里发光的鱼群，对琉花来说都像是一场浪漫的约定如期而至。'
            },

            // 我是用户的短评
            lists:[
                {   id:'1',deta:'da',titl:'aaa',up_num:999,down_num:999,},
                {   id:'2',deta:'das',titl:'aaaaaa',up_num:999,down_num:999,},
                {   id:'3',deta:'dsada',titl:'aaaaaaa',up_num:999,down_num:999,},
                {   id:'4',deta:'sda',titl:'aaaaaa',up_num:999,down_num:999,},
                {   id:'5',deta:'sdasda',titl:'a',up_num:999,down_num:999,},
            ],
            st_lists:[],
            hpn:1,
            // 我是用户的短评

        }
    },
    methods:
    {
         jmp(addr){
       this.$router.push({path:addr});
    },
         toRescue() {
        window.location.href = this.stuff.url;
      },
      toimdb() {
        window.location.href = this.stuff.imdb;
      },
    },
    created(){
        console.log("我是你的参数:"+parseInt(this.$route.params.id));
        this.IIDD=parseInt(this.$route.params.id);
        let params={op:2,hpno:this.IIDD};
        this.$http.post('/gethp.php',params).then(
                response=>{
                this.lists=[];
                var pllist=response.data
                console.log(pllist)
                this.hpn=pllist.length
                var i;
                for(i=0;i<pllist.length;i++){
                    var s1=pllist[i][0];
                    var s2=pllist[i][1];
                    var s3=pllist[i][2];
                    var s4=pllist[i][3];
                    var s5=pllist[i][4];
                    var tmp={id:s1,titl:s2,deta:s3,up_num:s4,down_num:s5};
                    this.lists.push(tmp);
                }
                this.st_lists=this.lists;
                }
        )
        let paramst={op:1,hpno:this.IIDD};
        this.$http.post('/getwork.php',paramst).then(
            response=>{
                console.log(response);
                var ss=response.data;
                this.stuff.picadd=ss[0];
                this.stuff.url=ss[1];
                this.stuff.imdb=ss[2];
                this.stuff.roughtime=ss[3];
                this.stuff.title=ss[4];
                this.stuff.director=ss[5];
                this.stuff.scriptor=ss[6];
                this.stuff.mainact=ss[7];
                this.stuff.type=ss[8];
                this.stuff.place=ss[9];
                this.stuff.dtime=ss[10];
                this.stuff.lang=ss[11];
                this.stuff.long=ss[12];
                this.stuff.webtxt=ss[13];
            }
        )
    }
}
